.Navigate{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 4rem;
    background-color: rgba(0, 39, 35, 0.95);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 40;
}

i{
    font-size: 3rem;
    color: white;
}

.filler{
    height: 4rem;
    background-color: black;
}

.image_pic{
    display: block;
    height: 3rem;
    margin-bottom: 0rem;
    margin-right: 0.5rem;
}

.hero{
    text-decoration: none;
    cursor: pointer;
    position: absolute;
    left: 1rem;
    display: flex;
    align-items: center;
    font-size: 1.9rem;
    color: rgb(255, 255, 255);
    font-weight: 500;
}

.hero:hover{
    color: rgb(168, 252, 243);
}

.tabOptions{
    font-size: 1.1rem;
    font-weight: 500;
    text-decoration: none;
    color: white;
    margin-left: 1rem;
    margin-right: 1rem;
}

.tabOptions:hover{
    color: rgb(51, 255, 231);
    font-weight: 500;
}

.reservation{
    cursor: pointer;
    color: white;
    position: absolute;
    right: 0rem;
    height: inherit;
    background-color: rgb(0, 88, 78);
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 1rem;
    padding-right: 1rem;
}

.reservation:hover{
    background-color: rgb(4, 119, 106);
}


/* -----------Card------------ */
.card{
    display: flex;
    flex-direction: column;
    padding: 1rem;  
    width: 15rem;
    height: 16rem;
    border: 2px solid rgb(116, 189, 180);
}

.card_title{
    display: flex;
    font-size: 1rem;
    font-weight: 600;
    color: rgb(0, 101, 89);
}

.card_desc p{
    margin-top: 1rem;
    text-align: left;
    color: rgb(81, 81, 81);
    font-size: 1rem;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 5;  
    overflow: hidden;   /* Hide the overflow */
    text-overflow: ellipsis;
    line-height: 1.6rem;
}


/* -----------Step------------ */

.step{
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 1rem;  
    width: 14rem;
    height: 14rem;
    border: 2px solid rgb(38, 175, 159);
    border-radius: 15px;
}

.step_title {
    display: flex;
    font-size: 1rem;
    font-weight: 700;
    color: rgb(0, 101, 89);
}

.step_desc p{
    margin-top: 2rem;
    text-align: left;
    color: rgb(81, 81, 81);
    font-size: 1.1rem;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 5;  
    overflow: hidden;   /* Hide the overflow */
    text-overflow: ellipsis;
    line-height: 1.6rem;
}

.step_no{
    position: absolute;
    bottom: 1rem;
    left: 2rem;
    display: flex;
    align-items: center;
}

.step_no p{
    font-size: 1.2rem;
    font-weight: 700;
    color: rgb(6, 130, 115);
}

.step img{
    margin-left: 0.5rem;
    margin-top: 0.1rem;
    height: 1.3rem;
    color: rgb(6, 130, 115);
}



/* -----------Home Page------------ */

.home{
    background: linear-gradient( rgba(0, 23, 20, 0.8), rgba(0, 23, 20, 0.8) ), url('../assets/doctors.jpg');
    background-size: cover;
    height: 100vh;
    width: 100;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.company_name{
    font-size: 1.5rem;
    font-weight: 400;
    color: white;
}

.entry_names{
    font-size: 5rem;
    font-weight: 400;
    color: white;
}

.package_contents{
    background-color: rgb(206, 225, 223);
    min-height: 80vh;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.package1{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}


/* -----------About page------------ */


.about_us{
    background: linear-gradient( rgba(0, 23, 20, 0.8), rgba(0, 23, 20, 0.8) ), url('../assets/microscope.jpg');
    background-size: cover;
    height: 104vh;
    width: 100;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.about_us .page_title{
    margin-top: 2.5rem;
    font-size: 4.1rem;
    font-weight: 400;
    color: white;
}

.content{
    width: 70%;
}

.content p{
    color: rgb(159, 255, 244);
    font-size: 1.5rem;
    font-style: italic;
    line-height: 3rem;
}

.steps_reservation{
    background-color: rgb(206, 225, 223);
    min-height: 80vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: baseline;
}

.steps_reservation .page_title{
    font-size: 3rem;
    font-weight: 400;
    color: rgb(0, 105, 94);
}

.steps_reservation .page_title p{
    margin-left: 3rem;
}

.steps_reservation .package_contents{
    min-height: 10vh;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 1rem;
    margin-bottom: 6rem;
}



/* -----------Doctors Page------------ */


.doctors{
    background: linear-gradient( rgba(0, 23, 20, 0.8), rgba(0, 23, 20, 0.8) ), url('../assets/kit.jpg');
    background-size: cover;
    min-height: 104vh;
    width: inherit;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.doctors .page_title{
    margin-top: .5rem;
    font-size: 4.1rem;
    font-weight: 400;
    color: white;
}

.doctors_list{
    width: 100%;
    height: fit-content;
    display: flex;
    justify-content: space-evenly;
    padding-top: 2rem;
    padding-bottom: 4rem;
}

.doctor_card{
    background-color: rgba(0, 0, 0, 0.347);
    width: 24rem;
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: center;
    padding-top: 1.3rem;
    padding-bottom: 1.8rem;
    border-radius: 15px;
}

.doctor_card p{
    text-align: left;
    margin-left: 1rem;
    font-size: 1rem;
    color: rgb(0, 194, 175);
}

.doctor_card .details{
    display: flex;
    align-items: center;
    margin-top: -1rem;
}
.doctor_card .details img{
    height: 1rem;
    margin-left: 2rem;
}

.doctor_card .name{
    color: rgb(0, 255, 225);
    font-size: 1.3rem;
    margin-left: 2rem;
    margin-bottom: 2rem;
}

.doctors_features{
    background-color: rgb(206, 225, 223);
    min-height: 80vh;
    display: flex;
    flex-direction: column;
    align-items: start;
    padding-top: 3rem;
}

.doctors_features div{
    display: flex;
    align-items: center;
    font-size: 1.1rem;
    font-weight: 700;
    color: rgb(8, 110, 99);
}

.doctors_features img{
    height: 1.5rem;
    margin-left: 2rem;
    margin-right: 2rem;
}


/* -----------Departments Page------------ */


.departments{
    background: linear-gradient( rgba(0, 23, 20, 0.8), rgba(0, 23, 20, 0.8) ), url('../assets/lab1.jpg');
    background-size: cover;
    min-height: 104vh;
    width: inherit;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.departments .page_title{
    margin-top: -2rem;
    font-size: 4.1rem;
    font-weight: 400;
    color: white;
}

.departments .intro p{
    color: rgb(101, 255, 240);
    margin-left: 10rem;
    margin-right: 10rem;
    font-size: 1.5rem;
    line-height: 3rem;
    font-style: italic;
}

.dept_names{
    padding-top: 4rem;
    background-color: rgb(206, 225, 223);
    margin-bottom: 0rem;
    padding-bottom: 4rem;
}

.all_departments{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 4rem;
}

.department_card {
    min-height: 20rem;
    width: 20rem;
    color: white;
    background-color: rgb(2, 37, 33, 0.9);
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: baseline;
    border-radius: 19px;
}

.department_card .name{
    font-size: 1.2rem;
    margin-top: 1.5rem;
    color: rgb(101, 255, 240);
}

.department_card .details{
    font-size: 1.1rem;
    text-align: left;
    margin-left: 1.5rem;
    margin-right: 1.5rem;
    margin-bottom: 2rem;
    color: rgb(223, 223, 223);
}


/* -----------Services Page------------ */


.services{
    background: linear-gradient(rgba(0, 23, 20, 0.8), rgba(0, 23, 20, 0.8) );
    background-size: cover;
    min-height: 104vh;
    width: inherit;
    display: flex;
    justify-content: start;
    align-items: center;
    flex-direction: column;
}

.services .page_title{
    margin-top: -1rem;
    font-size: 4.1rem;
    font-weight: 400;
    color: white;
}

.service_card {
    min-height: 10rem;
    width: 20rem;
    color: white;
    background-color: rgba(0, 0, 0, 0.399);
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: baseline;
    border-radius: 19px;
}

.service_card .name{
    font-size: 1.2rem;
    margin-top: 1.5rem;
    color: rgb(101, 255, 240);
}

.service_card .details{
    font-size: 1.1rem;
    text-align: left;
    margin-left: 1.5rem;
    margin-right: 1.5rem;
    margin-bottom: 2rem;
    color: rgb(223, 223, 223);
    line-height: 2rem;
}